.sk-wave {
  margin: 40px auto;
  width: 50px;
  height: 40px;
  text-align: center;
  font-size: 10px;
}

.sk-wave .sk-rect {
  background-color: #F6A52F;
  height: 100%;
  width: 6px;
  display: inline-block;
  animation: sk-waveStretchDelay 1.2s infinite ease-in-out;
}

.sk-wave .sk-rect1 {
  animation-delay: -1.2s;
}

.sk-wave .sk-rect2 {
  animation-delay: -1.1s;
}

.sk-wave .sk-rect3 {
  animation-delay: -1s;
}

.sk-wave .sk-rect4 {
  animation-delay: -0.9s;
}

.sk-wave .sk-rect5 {
  animation-delay: -0.8s;
}

@keyframes sk-waveStretchDelay {
  0%, 40%, 100% {
    transform: scaleY(0.4);
  }

  20% {
    transform: scaleY(1);
  }
}

.header {
  overflow: hidden;
}

.header .logo {
  background: #222D32 url("/ui/static/images/header.png?version=1") no-repeat center !important;
  background-size: 175px !important;
  float: left;
  width: 190px;
  -webkit-transition: width 0.5s ease;
  -moz-transition: width 0.5s ease;
  -o-transition: width 0.5s ease;
  transition: width 0.5s ease;
}

.header .logo a {
  height: 50px;
  display: block;
  padding: 5px;
}

.header .admin-toolbar {
  overflow: hidden;
  max-height: 50px;
  min-height: 50px;
}

.header .admin-toolbar md-icon {
  color: white;
  font-size: 20px;
}

.header .admin-toolbar md-menu {
  padding: 0px;
}

.header .admin-toolbar .md-button {
  min-width: inherit;
}

@media screen and (max-width: 1200px) {
  .header .logo {
    float: none;
    width: auto;
    border: 0;
  }
}

.nav-menu {
  position: relative;
  min-width: 190px !important;
  max-width: 190px !important;
  -webkit-transition: min-width 0.5s ease,max-width 0.5s ease;
  -moz-transition: min-width 0.5s ease,max-width 0.5s ease;
  -o-transition: min-width 0.5s ease,max-width 0.5s ease;
  transition: min-width 0.5s ease,max-width 0.5s ease;
}

.nav-menu md-content {
  background-color: #222d32 !important;
}

.nav-menu md-list-item.header {
  padding: 10px 25px 10px 15px;
  font-size: 12px;
  color: #4b646f;
  background: #1a2226;
}

.nav-menu md-list-item {
  min-height: 20px;
}

.nav-menu md-list {
  padding-top: 0;
}

.nav-menu .md-button {
  border-left: 3px solid transparent;
  color: #b8c7ce;
  padding: 12px 5px 12px 15px;
  margin: 0;
  text-align: left;
  line-height: 20px;
  text-transform: none;
  border-radius: 0;
}

.nav-menu .md-button:hover {
  color: #fff;
  background: #1e282c !important;
